.avatar__edit-icon {
    width: 1.625rem;
    height: 1.625rem;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2.983rem;
    border-radius: 100%;
    background-clip: padding-box;
    cursor: pointer;
    opacity: 0;
}

.avatar__edit-icon:hover {
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 1;
}
